@import "assets/scss/variables.scss";

.wrapper {
  padding: 7.5rem 0;
  color: $textDark;
  text-align: center;
  background-color: $bgLight;
  & .image-wrapper {
    width: 50%;
    position: relative;
    @include lg {
      width: 100%;
    }
    & img {
      width: 100%;
    }
    & .seal {
      position: absolute;
      right: 2rem;
      top: 1rem;
      width: 5rem;
      height: 5rem;
      @include lg {
        top: auto;
        bottom: 4.5rem;
      }
    }
  }
  & .text-wrapper {
    width: 50%;
    text-align: left;
    flex-direction: column;
    justify-content: flex-end;
    @include lg {
      width: 100%;
    }
    & h2 {
      font-size: $textLarge + 5px;
      font-family: $secondaryFont;
      letter-spacing: 1px;
      @include lg {
        margin-top: 3.5rem;
      }
      @include lg {
        font-size: $textLarge + 12.5px;
        text-align: center;
      }
    }
    & > p {
      font-size: $textSmall;
      margin-bottom: 2.5rem;
      opacity: 0.65;
      @include lg {
        margin-top: 3.5rem;
        text-align: center;
      }
    }
    & .sign {
      height: 5rem;
      margin-left: 2.5rem;
    }
    & .avatar-sign-wrapper {
      justify-content: center;
      margin-top: 5rem;
    }
  }
  & :global(.container-global) {
    @include lg {
      flex-direction: column;
    }
  }
}
