// Breakpoints
$screen-xsm-min: 380px;
$screen-sm-min: 480px;
$screen-md-min: 768px;
$screen-lg-min: 992px;
$screen-xl-min: 1200px;
$screen-xl2-min: 1281px;
$screen-xxl-min: 1671px;

// Media query mixins
@mixin xxl {
  @media (max-width: #{$screen-xxl-min}) {
    @content;
  }
}

@mixin xl2 {
  @media (max-width: #{$screen-xl2-min}) {
    @content;
  }
}

@mixin xl {
  @media (max-width: #{$screen-xl-min}) {
    @content;
  }
}

@mixin lg {
  @media (max-width: #{$screen-lg-min}) {
    @content;
  }
}

@mixin md {
  @media (max-width: #{$screen-md-min}) {
    @content;
  }
}

@mixin sm {
  @media (max-width: #{$screen-sm-min}) {
    @content;
  }
}

@mixin xsm {
  @media (max-width: #{$screen-xsm-min}) {
    @content;
  }
}
